<template>
  <div class="home d-flex justify-content-center align-items-center bg-first">
    <div class="welcome">
      <div>
        <input type="text" v-model="user">
      </div>
      <div @click="addBtn" class="text-center mt-3 border bg-white mx-5 p-1 rounded font-weight-bold">添加</div>
      <ul class="mt-3">
        <li v-for="(item, index) in userList" :key="index" class="mb-2 text-gray d-flex justify-content-between">
          <p class="font-weight-bold h6">{{ index + 1 }}. {{ item }}</p>
          <div @click="del(index)">移除</div>
        </li>
      </ul>
      <div @click="goToTurn" class="text-center mt-3 border bg-white mx-5 p-1 rounded font-weight-bold">GO!</div>
      <div>
      </div>
    </div>
    <div v-if="showTurntable" class="model-frame d-flex align-items-center p-4">
      <div class="turntable-frame mx-auto bg-first-light text-center rounded position-relative">
        <div class="close-btn bg-first text-white d-flex justify-content-center align-items-center position-absolute" @click="showTurntable = false">X</div>
        <div>
          <div class="turntable position-relative">
            <img src="../assets/img/ARROW.png" id="arrow" width="35%" alt="" class="position-absolute arrow" @click="turn">
            <img src="../assets/img/turntabletop.png" width="100%" alt="" class="position-absolute">
            <img src="../assets/img/turntable1.png" width="100%" alt="">
            <!-- <div class="arrow"></div>
            <ul class="mx-auto list-unstyled">
              <li v-for="(item, index) in userList" :key="index">{{ item }}</li>
            </ul> -->
          </div>
          <div @click="turn" class="go-btn text-center mt-3 border bg-white mx-5 p-1 rounded font-weight-bold">go!</div>
        </div>
      </div>
    </div>
  <div v-if="notice" class="notice-model d-flex align-items-center justify-content-center">
    <div class="bg-white rounded px-5 py-3 text-center">
      <p class="h6">your prize:</p>
      <p class="h6 mb-2">{{ this.random }}</p>
      <div @click="cloceNotice" class="bg-first-light px-2 py-1 text-white">close</div>
    </div>
  </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
      user: '',
      userList: [],
      showTurntable: false,
      play: false,
      time: null,
      seconddegree: 0,
      timer: 10,
      notice: false
    }
  },
  mounted () {
  },
  methods: {
    addBtn () {
      if (this.user === '') return
      this.userList.push(this.user)
      console.log(this.userList)
      this.user = ''
    },
    del (index) {
      this.userList.splice(index, 1)
    },
    goToTurn () {
      this.showTurntable = true
    },
    turn () {
      this.play = true
      // console.log('turn', document.querySelector('#arrow'))
      // console.log(this.time)
      this.random = Math.floor(Math.random() * (5 - 1 + 1)) + 1
      console.log(this.random)
      // this.seconddegree += 1
      // console.log('secon', this.seconddegree)
      this.time = setInterval(this.clock, this.timer)
      // if (this.seconddegree === 1800) {
      //   window.clearInterval(time)
      // }
    },
    clock () {
      this.$nextTick(() => {
        this.seconddegree += 1
        // const seconddegree = second * 6
        document.querySelector('#arrow').style.transform = `rotate(${this.seconddegree}deg)`
        // console.log(document.querySelector('#arrow').style.transform)
        if (this.seconddegree > 720 && this.seconddegree <= 1180) {
          this.timer = 11
        } else if (this.seconddegree > 1180 && this.seconddegree <= 1360) {
          this.timer = 12
        } else if (this.seconddegree > 1360 && this.seconddegree <= 1440) {
          this.timer = 13
        } else if (this.seconddegree > 1440) {
          this.timer = 17
        }
        window.clearInterval(this.time)
        this.time = setInterval(this.clock, this.timer)
        if (this.seconddegree === 1404 + (72 * this.random)) {
          window.clearInterval(this.time)
          this.notice = true
        }
      })
    },
    cloceNotice () {
      this.notice = false
      this.seconddegree = 0
      document.querySelector('#arrow').style.transform = `rotate(0deg)`
      this.timer = 1
    }
  }
}
</script>

<style lang="scss">
.home{
  height: 100vh;
  input{
    outline: none;
    border: none;
    border-radius: 15px;
    padding: 5px 15px;
  }
  .model-frame{
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    .turntable-frame{
      height: 100%;
      width: 100%;
    }
    .close-btn{
      width: 25px;
      height: 25px;
      border-radius: 50%;
      top: 1%;
      right: 1%;
    }
    .go-btn{
      bottom: 5%
    }
    .turntable{
      position: absolute;
      ul{
        background: gray;
        border: 2px solid gray;
        width: 300px;
        height: 300px;
        border-radius: 50%;
        overflow: hidden;
        position: absolute;
        margin: 0;
      }
      li{
        height: 150px;
        width: 150px;
        position: absolute;
        transform-origin: 100% 100%;
        border: gray 1px solid;
      }
      li:nth-child(odd) {
        background-color: #e6a7a7;
        // transform: rotate(0deg) skew(30deg);
      }
      li:nth-child(1) {
        // background-color: #e6a7a7;
        transform: rotate(0deg);
      }
      li:nth-child(even) {
        background-color: #429489;
        // transform: rotate(60deg) skew(30deg);
      }
    }
    .arrow{
    }
  }
}
</style>



