<template>
  <div class="game bg-first d-flex align-items-center justify-content-center">
    <transition name="fade">
      <div  v-if="gameshow" class="frame bg-first d-flex align-items-center justify-content-center">
        <div class="w-85 mh-85 p-3 mx-auto bg-first-light align-items-center justify-content-center">
          <div class="bg-white font-weight-bold p-3 text-second h7 my-3 rounded">
            <div class="d-flex justify-content-center">
              <p class="text-center text-wrap"> {{rewardList[index].name}} </p>
            </div>
          </div>
          <div class="w-75 mx-auto">
            <div class="d-flex justify-content-center text-gray text-center mb-3">
              <div @click="start" class="enter-btn bg-first-light h7 w-100 font-weight-bold">開始抽獎</div>
            </div>
            <div class="d-flex justify-content-between text-gray text-center">
              <div @click="restart" class="enter-btn bg-first-light w-100 mr-1 h6 font-weight-bold">重抽一次</div>
              <div @click="endgame" class="enter-btn bg-first-light w-100 h6 font-weight-bold">結束抽獎</div>
            </div>
          <div class="bg-first text-center p-1 text-white mt-3 rounded position-relative" @click="openRule">
            規則說明
            <span id="rule" class="ml-2 triangle position-absolute"></span>
          </div>
          <transition name="slide-fade">
            <div v-if="showRule" class="p-2 text-dark-gray">
              開始抽獎:抽過的獎項將不重複出現且刪除。<br>
              再抽一次:該次抽獎將被保留且繼續再抽下一個。<br>
              若需更改獎項名稱可自行更改刪除
            </div>
          </transition>
          <div class="bg-first text-center p-1 text-white mt-3 rounded position-relative" @click="openSort">
            獎項順序
            <span id="sort" class="ml-2 triangle position-absolute"></span>
          </div>
          <transition name="slide-fade">
            <div v-if="showSort" class="p-2 text-dark-gray">
              <p v-for="(item, index) in sortList" :key="index">{{ index + 1 }}.{{ item }}</p>
            </div>
          </transition>
          </div>
          <!-- <div @click="recover" class="w-50 enter-btn mb-2 bg-white text-gray text-center rounded mx-auto text-center">全部重來</div> -->
          <div @click="backHome" class="w-50 mt-3 enter-btn bg-white text-gray text-center rounded mx-auto text-center">返回</div>
        </div>
      </div>
    </transition>
    <div class="text-center w-85">
      <p class="h7 mb-3 text-white">幸運抽獎器</p>
      <div class="d-flex justify-content-between align-items-center mb-3">
        <input type="text" @keyup.enter="addname" v-model="name" placeholder="Enter your reward">
        <input type="checkbox" v-model="isrepeat" class="mx-2">
        <div @click="addname" class="enter-btn bg-first-light text-gray rounded font-weight-bold">Add</div>
      </div>
      <div v-if="keepList.length > 0" class="text-left p-2 bg-first-light name-list">
        <div v-for="(item,index) in keepList" :key="index" class=" m-2 d-flex justify-content-between align-items-center">
          <p class="text-gray w-50 mb-0 text-wrap">{{ index + 1 }}. {{ item.name }}</p>
          <p class="text-white mb-0">{{ item.repeat?'Repeat':'' }}</p>
          <p @click="delName(index)" class="text-first p-1 rounded h5 mb-0 bg-white">Del</p>
        </div>
      </div>
      <div v-if="keepList.length > 0" @click="goToStart" class="mt-3 mx-5 enter-btn bg-first-light text-center text-gray">START</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'game',
  data () {
    return {
      rewardList: [],
      keepList: [
        { name: 'NEXT', repeat: true },
        { name: 'Han', repeat: false },
        { name: 'Chen', repeat: false },
        { name: 'Li', repeat: false },
        { name: 'Ling', repeat: false },
        { name: 'Kang', repeat: false }
      ],
      sortList: [],
      index: 0,
      isnorepeat: false,
      name: '',
      isrepeat: false,
      gameshow: false,
      showRule: false,
      showSort: false,
      disable: false,
      gaming: false
    }
  },
  methods: {
    // keepList 為固定資料唯有新增刪除會影響
    addname () {
      if (this.gaming) {
        alert('已開始抽獎 請先結束再新增')
        return
      }
      if (this.name === '') {
        alert('請輸入內容')
        return
      }
      this.keepList.push({name: this.name, repeat: this.isrepeat})
      this.$nextTick(() => {
        const nameframe = document.querySelector('.name-list')
        nameframe.scrollTop = nameframe.scrollHeight
      })
      this.name = ''
      this.isrepeat = false
    },
    delName (index) {
      if (this.gaming) {
        alert('已開始抽獎 請先結束再刪除')
        return
      }
      this.keepList.splice(index, 1)
    },
    goToStart () {
      this.gameshow = true
      if (this.rewardList.length < 1) {
        this.rewardList.push(...this.keepList)
      } else if (!this.gaming) {
        this.rewardList = []
        this.rewardList.push(...this.keepList)
      }
    },
    recover () {
      // this.rewardList = this.keepList
    },
    openRule () {
      this.showRule = !this.showRule
      document.querySelector('#rule').style.transform = document.querySelector('#rule').style.transform === 'rotate(0deg)' ? 'rotate(-180deg)' : 'rotate(0deg)'
    },
    openSort () {
      this.showSort = !this.showSort
      document.querySelector('#sort').style.transform = document.querySelector('#sort').style.transform === 'rotate(0deg)' ? 'rotate(-180deg)' : 'rotate(0deg)'
    },
    restart () {
      if (!this.gaming) {
        alert('請先開始')
        return
      }
      if (this.isnorepeat && !this.rewardList[this.index].repeat) {
        this.sortList.pop()
        console.log('145')
      }
      this.isnorepeat = false
      this.start()
    },
    endgame () {
      if (!this.gaming) return
      this.gaming = false
      this.isnorepeat = false
      this.sortList = []
      this.rewardList = []
      this.rewardList.push(...this.keepList)
      console.log('157')
    },
    start () {
      // if (this.disable) return
      // 遊戲開始運行 需結束後才能新增刪除
      // this.disable = true
      // if (this.rewardList[this.index].repeat <= 1) {
      //   alert('已結束 請返回重新開始')
      //   return
      // }
      if (this.isnorepeat && !this.rewardList[this.index].repeat) {
        console.log('158')
        this.rewardList.splice(this.index, 1)
      }
      this.gaming = true
      this.isnorepeat = true
      for (let j = 1; j <= 5; j++) {
        setTimeout(() => {
          // console.log('34', j)
          for (let i = 0; i < this.rewardList.length; i++) {
            setTimeout(() => {
              if (j === 5 && i === this.rewardList.length - 1) {
                // console.log('theend')
                let max = this.rewardList.length - 1
                let min = 0
                this.index = Math.floor(Math.random() * (max - min + 1) + min)
                if (!this.rewardList[this.index].repeat) {
                  console.log('184')
                  this.sortList.push(this.rewardList[this.index].name)
                }
                // this.disable = false
              } else {
                console.log('189')
                this.index = i
              }
            }, 100 * i)
          }
        }, 150 * j)
      }
    },
    backHome () {
      this.gameshow = false
    }
  }
}
</script>

<style lang="scss">
  .game{
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    word-break: break-all;
    .frame{
      height: 100%;
      width: 100%;
      position: absolute;
      top: 0;
    }
    input{
      background: white;
      border: 1px solid gray;
      border-radius: 15px;
      padding: 5px 15px;
      height: 100%;
      color:gray;
      outline: none;
    }
    input[type="checkbox"]{
      height: 20px;
    }
  }
  .w-85{
    width: 85%;
  }
  .mh-85{
    max-height: 85%;
    overflow: scroll;
  }
  .enter-btn {
    border: white 1px solid;
    padding: 5px 15px;
    outline: none;
  }
  .name-list{
    height: 250px;
    overflow: scroll;
  }
  /* 下拉三角形 */
.triangle{
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 12px 8.5px 0 8.5px;
  border-color: #ffffff transparent transparent transparent;
  margin-top: 5px;
  transition: 0.5s;
  transform: rotate(-180deg);
}
/* 可以设置不同的进入和离开动画 */
/* 设置持续时间和动画函数 */
.slide-fade-enter-active {
  transition: all .3s ease-in-out;
}
.slide-fade-leave-active {
  transition: all .3s ease-in-out;
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */ {
  transform: translateY(-10px);
  opacity: 0;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .2s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

</style>



