import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home.vue'
import Game from '@/views/Game.vue'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/',
      name: 'Game',
      component: Game
    }
  ]
})



